import axios from 'axios';

const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? 'http://localhost:7542/2.0/' : 'https://neko-back.herokuapp.com/2.0/',
  withCredentials: true
})

export const cardsApi = {
  getCards(data: QueryParamsCardsType) {
    return instance.get<CardsResponseType>('cards/card', {params: data})
  },
  createCard(data: PostCardType) {
    return instance.post('cards/card', {card: data})
  },
  updateCard(data: UpdateCardType) {
    return instance.put('cards/card', {card: data})
  },
  deleteCard(cardId: string) {
    return instance.delete('cards/card', {params: {id: cardId}})
  },
}

export type CardType = {
  answer: string
  question: string
  cardsPack_id: string
  grade: number
  shots: number
  user_id: string
  created: string
  updated: string
  _id: string
}
export type CardsResponseType = {
  cards: Array<CardType>
  packUserId: string,
  packName: string,
  packPrivate: boolean,
  packCreated: string,
  packUpdated: string,
  page: number,
  pageCount: number,
  cardsTotalCount: number,
  minGrade: number,
  maxGrade: number,
  token: string,
  tokenDeathTime: number
}
export type QueryParamsCardsType = {
  cardAnswer?: string
  cardQuestion?: string
  cardsPack_id: string
  min?: number
  max?: number
  sortCards?: string
  page?: number
  pageCount?: number
}
export type PostCardType = {
  cardsPack_id: string
  question?: string
  answer?: string
  grade?: number
  shots?: number
  answerImg?: string
  questionImg?: string
  questionVideo?: string
  answerVideo?: string
}
export type UpdateCardType = {
  _id: string
  question?: string
}